* {
    margin: 0px;
    padding:0px;
    box-sizing: border-box;
}
body{
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.app{
    background-image: url(spring.jpg);
    background-size: cover;
    background-position: bottom;
    transition: 0.4 easet;
}
.app.warm{
    background-image: url(warm.jpg);
}
.app.winter{
    background-image: url(winter.jpg);
}

main{
    min-height: 100vh;
    background-image: linear-gradient(to bottom, rgba(0,0,0,0.2),rgba(0,0,0,0.75));
    padding:25px;
}
.s-box{
    width:70%;
    margin:0% 20% 0% 20%;
  
    
      
}
.s-box .s-bar{
    display: block;
    width: 100%;
    padding:15px;
    background-color: rgba(255,255,255,0.5);
    
    border-radius: 0px 0px 16px 16px;
    margin: -25px;
    box-shadow: 0px 5px rgba(0,0,0,0.25);
    color: black;
    font-size: medium;
    transition: 0.4s ease;

}
.s-box .s-bar:focus{
    background-color: rgba(255,255,255,0.75);
}

.l-box .lc{
    color: seashell;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 3px rgba(50,50,70,0.5);
}
.l-box .date{
    color: seashell;
    font-size: 20px;
    margin:15px;
    font-weight: 300;
    font-style: italic;
    text-align: center;
    text-shadow: 3px 3px rgba(50,50,70,0.5);
}
.w-box{ text-align: center;}

.w-box .temp{
    position: relative;
    display: inline-block;
    margin: 30px auto;
    background-color: rgba(255,255,255,0.25);
    border-radius: 16px;
    padding: 15px 25px;
    color: silver;
    font-size: 102px;
    font-weight: 900;
    text-shadow: 3px 6px rgba(50,50,70,0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(0,0,0,0.25);

}
.w-box .weather{
    color: silver;
    font-size: 48px;
    font-weight: 700;
    text-shadow: 3px 6px rgba(50,50,70,0.5);

}
@media screen and (max-width:600px)
{
    
.app{
    background-image: url(spring-mobile.jpg);
    background-size: cover;
    background-position: bottom;
    transition: 0.4 ease-out;
}
.app.warm{
    background-image: url(warm-mobile.jpg);
}
.app.winter{
    background-image: url(winter-mobile.jpg);
}
}